import axios from 'axios';
const WORKERS_URL = (process.env.NODE_ENV !== 'development')
    ? '/workers'
    : '//localhost:8080/workers';


export const getWorkers = async (criteria) => {
    return await axios.post(WORKERS_URL, criteria)
};

export const getWorkerById = async (id) => {
    return await axios.get(`${WORKERS_URL}/${id}`);
};

export const updateRating = async (workerId, ratingInfo) => {
    // return await axios.post(`${WORKERS_URL}/${workerId}/update-rating`, ratingInfo, { withCredentials: true });
    return await axios.post(`${WORKERS_URL}/${workerId}/update-rating`, { ratingInfo });
};

export const addComment = async (data) => {
    return await axios.post(`${WORKERS_URL}/add-comment`, { data });
};

export const deleteComment = async (data) => {
    return await axios.delete(`${WORKERS_URL}/delete-comment`, { data });
}

export const updateComment = async (data) => {
    return await axios.put(`${WORKERS_URL}/update-comment`, { data });
}