import React, { useState } from 'react'
import Avatar from '@material-ui/core/Avatar';
import styles from './CommentRow.module.scss';
import * as moment from 'moment';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import { Button } from '@material-ui/core';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import { useDispatch, useSelector } from 'react-redux';
import * as actions from '../../../store/actions/index';
import AddComment from '../AddComment/AddComment';
import { updateComment } from '../../../services/workersService';


const CommentsRow = ({ comment }) => {
    const [anchorEl, setAnchorEl] = useState(null);
    const workerState = useSelector(state => state.workerReducer.worker);
    const [isEditMode, setIsEditMode] = useState(false);
    const dispatch = useDispatch();


    const getDateTimeCorrect = () => {
        return moment("20111031", "YYYYMMDD").fromNow();
    }

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const handleDeleteComment = () => {
        const data = {
            commentId: comment.commentId,
            workerId: workerState._id
        };
        dispatch(actions.deleteCommentHandler(data))
    }

    const opneEditMode = () => {
        handleClose();
        setIsEditMode(true);
    }

    const onUpdateComment = (commentText) => {
        console.log({ comment });
        setIsEditMode(false);
        const data = {
            ...comment,
            comment: commentText
        };
        dispatch(actions.updateCommentHandler(data));
    }

    return (
        <div className={styles.commentRow}>
            {!isEditMode && <>
                <Avatar />
                <div className={styles.commentDetails}>
                    <div>
                        <p><span className="bold">{comment.userFirstName} {comment.userLastName}</span> <span className="black-light">לפני שנתיים</span></p>
                        <p>{comment.comment}</p>
                    </div>
                    <Button size="small" onClick={handleClick}>
                        <MoreVertIcon />
                    </Button>

                    <Menu
                        id="lock-menu"
                        anchorEl={anchorEl}
                        keepMounted
                        open={Boolean(anchorEl)}
                        onClose={handleClose}
                    >
                        <MenuItem onClick={handleDeleteComment}>
                            מחק
                    </MenuItem>
                        <MenuItem onClick={opneEditMode}>
                            עדכן
                    </MenuItem>
                    </Menu>
                </div>
            </>}
            {
                isEditMode && <AddComment onDispatchComment={onUpdateComment} valueInput={comment.comment} onCancel={() => setIsEditMode(false)} />
            }
        </div >
    )
}

export default CommentsRow
