import { getWorkers, addComment, getWorkerById, deleteComment, updateComment } from '../../../services/workersService';
import * as actionTypes from './actionTypes';

const _loadWorkers = () => {
    return {
        type: actionTypes.LOAD_WORKERS,
    }
}

const _loadWorkersSuccess = (data) => {
    return {
        type: actionTypes.LOAD_WORKERS_SUCCESS,
        payload: data
    }
}

const _loadWorkersFail = (error) => {
    return {
        type: actionTypes.LOAD_WORKERS_FAIL,
        payload: error
    }
}

export const loadWorkers = (criteria) => async (dispatch) => {
    dispatch(_loadWorkers())
    try {
        const { data } = await getWorkers(criteria);
        dispatch(_loadWorkersSuccess(data))
    } catch (e) {
        console.log(e);
        dispatch(_loadWorkersFail(e))
    }
};

export const updateCriteria = (criteria) => {
    return {
        type: actionTypes.UPDATE_CRITERIA,
        payload: criteria
    }
}

export const updateInitialPage = (selectedPage) => {
    return {
        type: actionTypes.UPDATE_INITIAL_PAGE,
        payload: selectedPage
    }
}

export const clearDataWorkers = () => ({ type: actionTypes.CLEAR_DATA_WORKERS });

const _addComment = (comments) => {
    return {
        type: actionTypes.ADD_COMMENT,
        payload: comments
    }
};

export const addCommentHandler = (dataComment) => async dispatch => {
    try {
        const { data } = await addComment(dataComment);
        dispatch(_addComment(data));
    } catch (err) {
        throw new Error('Error on add new comment', err);
    }
};

const _deleteCommentSuccess = (data) => {
    return {
        type: actionTypes.DELETE_COMMENT_SUCCESS,
        payload: data
    }
}

const _deleteComment = (data) => {
    return {
        type: actionTypes.DELETE_COMMENT,
        payload: { data, action: 'DELETE' }
    }
}

export const deleteCommentHandler = (dataComment) => async dispatch => {
    const { commentId } = dataComment;
    dispatch(_deleteComment(dataComment));
    try {
        const { data } = await deleteComment(dataComment);
        if (data) {
            dispatch(_deleteCommentSuccess(commentId))
        }
    } catch (err) {
        console.log(`DELETE COMMENT ERROR`, err);
    }
}

export const _updateCommentSuccess = (data) => {
    return {
        type: actionTypes.UPDATE_COMMENT_SUCCESS,
        payload: data
    }
}

export const _updateComment = (data) => {
    return {
        type: actionTypes.UPDATE_COMMENT,
        payload: { data, action: 'UPDATE' }
    }
}

export const updateCommentHandler = (dataComment) => async dispatch => {
    dispatch(_updateComment(dataComment))
    try {
        const { data } = await updateComment(dataComment);
        if (data) {
            dispatch(_updateCommentSuccess(dataComment));
        }
    } catch (err) {
        console.log(`UPDATE COMMENT ERROR`, err);

    }
}

// Single worker 

const _loadWorker = () => {
    return {
        type: actionTypes.LOAD_WORKER
    }
};

const _loadWorkerSuccess = (data) => {
    return {
        type: actionTypes.LOAD_WORKER_SUCCESS,
        payload: data
    }
}

const _loadWorkerFail = (error) => {
    return {
        type: actionTypes.LOAD_WORKER_FAIL,
        payload: error
    }
};



export const loadWorkerById = (id) => async dispatch => {
    dispatch(_loadWorker());
    try {
        const { data } = await getWorkerById(id);
        if (data) {
            dispatch(_loadWorkerSuccess(data))
        }
    } catch (err) {
        dispatch(dispatch(_loadWorkerFail(err)));
        throw new Error(err);
    }
};