import React, { memo } from 'react'
import CommentsRow from '../CommentsRow/CommentsRow';
import CircularProgress from '@material-ui/core/CircularProgress';

const CommentsList = ({ comments, isLoadingUpdateComment, isFinishedUpdateComment, currentCommentUpdated }) => {

    const renderListComments = (comment) => {
        if (isLoadingUpdateComment && !isFinishedUpdateComment && currentCommentUpdated && currentCommentUpdated.commentId === comment.commentId) {
            return <CircularProgress thickness={6.5} size={50} />
        } else {
            return <CommentsRow comment={comment} />
        }
    }

    return (
        <div>
            {comments.map((comment) => (
                <div key={comment.commentId}>
                     {renderListComments(comment)}
                </div>
            ))}
        </div>
    )
}

export default memo(CommentsList);