import * as actionTypes from '../../actions/actionsWorkers/actionTypes';

const initialState = {
    worker: null,
    isLoading: false,
    isFinished: false, // handle for CircularProgress (need to know when is finished)
    error: null,
    isLoadingUpdateComment: false,
    isFinishedUpdateComment: false,
    currentCommentUpdated: null,
    action: null
};


const reducer = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.LOAD_WORKER:
            return {
                ...state,
                isLoading: true,
                isFinished: false
            }
        case actionTypes.LOAD_WORKER_SUCCESS:
            return {
                ...state,
                isLoading: false,
                isFinished: true,
                worker: action.payload,
            }
        case actionTypes.LOAD_WORKER_FAIL:
            return {
                ...state,
                error: action.payload,
                isFinished: true,
                isLoading: false
            }
        case actionTypes.ADD_COMMENT:
            return {
                ...state,
                worker: {
                    ...state.worker,
                    comments: [action.payload, ...state.worker.comments]
                }
            }
        case actionTypes.DELETE_COMMENT_SUCCESS:
            return {
                ...state,
                worker: {
                    ...state.worker,
                    comments: state.worker.comments.filter(c => c.commentId !== action.payload)
                },
                isLoadingUpdateComment: false,
                isFinishedUpdateComment: true,
            }
        case actionTypes.UPDATE_COMMENT:
        case actionTypes.DELETE_COMMENT:
            return {
                ...state,
                isLoadingUpdateComment: true,
                isFinishedUpdateComment: false,
                currentCommentUpdated: action.payload.data,
                action: action.payload.action
            }
        case actionTypes.UPDATE_COMMENT_SUCCESS:
            const commentIndex = state.worker.comments.findIndex(c => c.commentId === action.payload.commentId);
            const updatedComments = [...state.worker.comments];
            updatedComments[commentIndex] = action.payload;
            return {
                ...state,
                worker: {
                    ...state.worker,
                    comments: updatedComments
                },
                isLoadingUpdateComment: false,
                isFinishedUpdateComment: true,
                currentCommentUpdated: null
            }
        case actionTypes.CLEAR_DATA_WORKER_DETAILS:
            return initialState;
        default:
            return state;
    }
};

export default reducer;