import React, { useState, useEffect } from 'react'
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Avatar from '@material-ui/core/Avatar';
import styles from './AddComment.module.scss';

const AddComment = ({ onDispatchComment, valueInput, onFocusHandler, onCancel }) => {
    const [commentText, setCommentText] = useState('');

    const onAddCommentHandler = (ev) => {
        ev.preventDefault();
        if (commentText.length) {
            onDispatchComment(commentText);
            setCommentText('');
        }
    }

    useEffect(() => {
        if (valueInput && typeof valueInput === 'string') {
            setCommentText(valueInput);
        }
    }, [])

    const isValid = () => {
        return commentText.length > 0;
    };

    useEffect(() => {
        isValid();
    }, [commentText.length])

    const cancelHandler = () => {
        if (onCancel) {
            onCancel();
        } else {
            setCommentText('');
        }
    }

    return (
        <div>
            <form onSubmit={ev => onAddCommentHandler(ev)}>
                <section className={styles.sectionAdditionalComment}>
                    <Avatar />
                    <TextField
                        placeholder="תגובה חדשה..."
                        multiline
                        rowsMax="4"
                        onChange={e => setCommentText(e.target.value)}
                        onFocus={onFocusHandler}
                        value={commentText}
                        classes={{ root: 'addField' }}
                    />
                </section>
                <div className={styles.btnsActions}>
                    <Button variant="outlined" color="primary" size="small" onClick={cancelHandler}>
                        ביטול
                    </Button>
                    <Button variant="contained" color="primary" size="small" type="submit" disabled={!isValid()}>
                        תגובה
                    </Button>
                </div>
            </form>
        </div>
    )
}

export default AddComment
