import React, { useRef, useEffect, useState } from 'react'
import styles from './CommentsContainer.module.scss'
import CommentsList from '../CommentsList/CommentsList';
import { getUserLoggedIn } from '../../../services/authService';
import { useHistory } from 'react-router-dom';
import AddComment from '../AddComment/AddComment';
import { useSelector, useDispatch } from 'react-redux';
import * as actionTypes from '../../../store/actions/actionsWorkers/actionTypes';
import Snackbar from '@material-ui/core/Snackbar';

const CommentsContainer = ({ addCommentHandler, comments }) => {
    const [openSnackbar, setopenSnackbar] = useState(false);
    const isCheckuserLoggedInFirst = useRef(false);
    const history = useHistory();
    const dispatch = useDispatch();
    const isLoadingUpdateComment = useSelector(state => state.workerReducer.isLoadingUpdateComment);
    const isFinishedUpdateComment = useSelector(state => state.workerReducer.isFinishedUpdateComment);
    const actionType = useSelector(state => state.workerReducer.action);
    const currentCommentUpdated = useSelector(state => state.workerReducer.currentCommentUpdated);

    const onAddCommentHandler = (commentText) => {
        addCommentHandler(commentText);
    };

    const checkIfLogegdIn = async () => {
        if (isCheckuserLoggedInFirst.current) return;
        isCheckuserLoggedInFirst.current = true;
        const isUserLoggedIn = await getUserLoggedIn()
        if (!isUserLoggedIn) {
            history.push('/login')
        }
    };

    const handleClose = (event, reason) => {
        console.log(event, reason);

        if (reason === 'clickaway') {
            return;
        }
        setopenSnackbar(false);
    };

    useEffect(() => {
        console.log('Hey2');
        
        if (actionType === 'DELETE' && isFinishedUpdateComment) {
            setopenSnackbar(true)
        }
    }, [actionType, isFinishedUpdateComment])

    useEffect(() => {
        console.log('Hey1');
        
        return () => {
            dispatch({ type: actionTypes.CLEAR_DATA_WORKER_DETAILS })
        }
    }, [])


    return (
        <div className={styles.containerComments}>
            <AddComment onDispatchComment={onAddCommentHandler} onFocusHandler={checkIfLogegdIn} />
            <CommentsList comments={comments} isLoadingUpdateComment={isLoadingUpdateComment} isFinishedUpdateComment={isFinishedUpdateComment} currentCommentUpdated={currentCommentUpdated} />
            <Snackbar
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'left',
                }}
                open={openSnackbar}
                autoHideDuration={5000}
                onClose={handleClose}
                message="התגובה נמחקה" />
        </div>
    )
}

export default CommentsContainer;
